.nav__block {
  .list__total {
      margin-right: 30px;
      color: #bebebe; }
  .fa-share {
    color: #bebebe; } }
.list__tasks {
  position: relative;
  .list__add {
    border: none;
    top: -40px; }
  .list__task {
    display: flex;
    align-items: center;
    width: 100%;
    height: 80px;
    transition: background-color .3s ease;
    border-bottom: 1px solid #f3f3f4;
    &:hover {
      background-color: #cfcfd7; }
    a {
      padding-left: 50px;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      font-size: 23px;
      position: relative;
      &:before {
        font-family: FontAwesome;
        content: "\f00c";
        position: absolute;
        left: 11px; } } }
  .list__task_done {
    background-color: #f3f3f4;
    border-left: 5px solid #50d2c2;
    box-sizing: border-box;
    a {
      &:before {
        color: #50d2c2; } } } }
