body {
  font-size: 16px;
  font-family: Roboto;
  background-color: {}
  margin: 0;
  position: relative;
  line-height: 1.6;
  overflow-x: hidden;
  -webkit-tap-highlight-color: transparent; }
p,ul {
  padding: 0;
  margin: 0; }
li {
  list-style: none; }
a {
  color: #000;
  text-decoration-color: transparent;
  &:hover, &:focus {
    color: #000; }
  &:visited {
    outline: none; } }
h1 {
  margin: 0; }
